* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background-color: #faf9f8;
}

.github-fork-ribbon:focus {
  outline: 0;
}

.github-fork-ribbon:focus:before {
  outline: solid #000;
}

.github-fork-ribbon:before {
  background-color: #333;
}

.github-fork-ribbon:after {
  border-style: ridge;
}

a:link, a:visited {
  color: #005a9f;
}

#app {
  margin: .5rem auto;
  padding: 0 .5rem;
}

main {
  max-width: 96vw;
  margin: 0 auto;
}

kbd {
  border: outset #666;
  padding: 0 .25em;
  font-weight: bold;
  line-height: 2;
}

table {
  border-spacing: 0;
  background-color: #fff3;
  border: thin solid #333;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
}

td {
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  user-select: none;
  width: 2rem;
  min-width: 2rem;
  height: 2rem;
}

td button {
  width: 100%;
  height: 100%;
  color: inherit;
  background-color: #0000;
  border: thin solid #666;
  border-radius: 10%;
  font-family: Consolas, monospace;
  font-size: 1em;
  display: block;
}

td button:disabled:not(:empty) {
  border-color: #666;
}

.covered button {
  background-color: #9ac;
}

.covered button:enabled:hover, .covered button:enabled:focus-visible {
  background-color: #789;
  border-color: #333;
}

.covered button:after {
  content: attr(valu);
}

.revealed button:not(:empty) {
  background-color: #eee8aa;
}

.revealed button:empty {
  background-color: #ddd;
  border-color: #aaa;
}

.revealed button:enabled {
  cursor: pointer;
  font-weight: bold;
}

.revealed button[value="1"] {
  color: #0000a5;
}

.revealed button[value="2"] {
  color: #003700;
}

.revealed button[value="3"] {
  color: #9a0000;
}

.revealed button[value="4"] {
  color: #570057;
}

.revealed button[value="5"] {
  color: #640000;
}

.revealed button[value="6"] {
  color: #005454;
}

.revealed button[value="7"] {
  color: #000;
}

.maybe button {
  background: #a8e063 radial-gradient(#56ab2f, #a8e063) no-repeat;
}

.flagged button {
  color: #fff;
  background: #2f0047 radial-gradient(#5d1674, #2f0047) no-repeat;
}

.flagged-wrong button {
  color: #fff;
  background: orange radial-gradient(orange, #ff8c00) no-repeat;
}

.detonated button {
  background: red radial-gradient(red, #8b0000) no-repeat;
  border-radius: 2px;
  box-shadow: 2px 4px 4px #00000080;
}

.vulnerable button {
  background: #ff0 radial-gradient(gold, #ffffe0) no-repeat;
  border-radius: 2px;
}

.shadowUtil {
  box-shadow: 0 1.6px 3.6px #00000021, 0 .3px .9px #0000001c;
}

.gameStatus {
  text-align: center;
  width: 100%;
  padding: 0 .5em;
}

.gameStatus p {
  margin: .75em 1em;
}

.gameInfo {
  background-color: #fff;
  width: 100%;
  padding: 0 .5em;
}

.highScorePanel ol {
  counter-reset: highscores;
  min-width: 12rem;
  padding-left: .5em;
  list-style-type: none;
}

.highScorePanel ol li {
  counter-increment: highscores;
  border-top: thin solid #aaa;
  margin-bottom: .5em;
}

.highScorePanel ol li:hover {
  background-color: #ddd;
}

.highScorePanel ol li:first-child {
  font-weight: bold;
}

.highScorePanel ol li:before {
  content: counter(highscores);
  text-align: center;
  color: #fff;
  background-color: #333;
  border: thin solid #aaa;
  border-top: 0;
  width: 2ch;
  margin-right: .25em;
  padding: 0 .5em;
  font-weight: normal;
  display: inline-block;
}

.achievementsPanel {
  flex-direction: column;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.achievementsPanel .trophyIcon {
  margin-right: .5rem;
  font-size: 2em;
}

.achievementsPanel .notAchieved .achievementTitle {
  opacity: .8;
}

.achievementsPanel .notAchieved .trophyIcon, .achievementsPanel .notAchieved .achievementDescription {
  opacity: .3;
}

.achievementsPanel .notAchieved:hover .trophyIcon, .achievementsPanel .notAchieved:hover .achievementDescription {
  opacity: .8;
}

.achievementsPanel .achieved {
  background-color: #90ee90;
}

.achievementsPanel li {
  margin-bottom: 1rem;
  padding: .25rem;
  display: flex;
}

.achievementsPanel .achievementTitle {
  margin: 0;
}

.achievementsPanel .achievementDescription {
  font-size: smaller;
}

@media screen and (width >= 65rem) {
  #app {
    max-width: 65rem;
    margin: 2rem auto;
  }

  table {
    border-style: double;
    border-width: thick;
    padding: .25em;
  }

  .achievementsPanel {
    flex-flow: wrap;
    column-gap: .5em;
  }

  .achievementsPanel li {
    flex: calc(50% - .5em);
  }
}
